import './popup.scss';

function Popup(props) {
  return (
    <div className="popup">
      <div className="popup__wrapper">
        <header className="popup__header"> </header>
        <p className="popup__text">{props.children}</p>
        <footer className="popup__footer">
          <button onClick={props.onAgree} className="popup__ok"></button>
          <button onClick={props.onCancel} className="popup__cancel"></button>
        </footer>
      </div>
    </div>
  );
}

export default Popup;